.normal{
  margin: 0;
}

.heading{
  font-size: 2rem;
  font-weight: 600;
  margin: 5% 0 8px 0;
}

.color{
  color: #3cb371;
}

.title{
  font-size: 1.5rem;
  font-weight: 700;
  margin: 40px 0 0 0;
}

.sub-title{
  font-size: 1.2rem;
  font-weight: 600;
  margin: 10px 0 8px 0;
}

.mild{
  font-size: 1.2rem;
  font-weight: 400;
  margin: 10px 0 8px 0;
}

@media only screen and (min-width: 768px) {
  .title{
    margin: 10px 0 8px 0;
  }
  .sub-title{
    margin: 15px 0 8px 0;
  }
}



